.main-content{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    /* grid-template-rows: 1fr; */
    gap: 10px 10px;
    /* grid-template-areas: ". . . ."; */
    padding: 60px 10px;
    /* background-color: #f7f7f8; */
}

@media (max-width: 1300px) { 
    .main-content{
        grid-template-columns: 1fr 1fr;
    }
}

@media (max-width: 1000px) { 
    .main-content{
        grid-template-columns: 1fr;
    }
}