.tool-item {
    position: relative;
    padding: 20px;
    /* max-width: 500px; */
    background-color: #fff;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid rgba(0, 0, 0, 0.11);
    transition: all 0.2s;
    cursor: pointer;
    height: 280px;
}

.tool-item:hover {
    transform: translateY(-5px);
    -webkit-box-shadow: 0px 2px 5px 0px rgba(50, 50, 50, 0.3);
    -moz-box-shadow: 0px 2px 5px 0px rgba(50, 50, 50, 0.3);
    box-shadow: 0px 2px 5px 0px rgba(50, 50, 50, 0.3);
}

.tool-item__head {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    padding-right: 40px;
}

.tool-item__title {
    margin-left: 15px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.tool-item__icon {
    display: flex;
    align-items: center;
}

.tool-item__icon img {
    width: 32px;
    height: auto;
}

.tool-item__descr {
    max-height: 100px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin-bottom: 10px;
}

.tool-item__link {
    display: inline-block;
    margin-bottom: 20px;
}

.tool-item__link:hover {
    /* transform: scale(1.05); */
    transform: translate(5px);
}

.tool-item__tags {
    font-size: 14px;
    color: rgba(0, 0, 0, 0.7);
    position: relative;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    height: 42px;
    font-weight: 600;
}

