.tool-item__category {
    display: flex;
    justify-content: flex-end;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.65);
    font-weight: 700;
}

.tool-item__tags-item{
    margin-left: 8px;
}

.tool-item__tags-item:hover{
    transform: scale(1.1);
    color: #000;
}

.tool-item__tags-item:active{
}

.tool-item:hover .remove-btn{
    display: flex;
}

.remove-btn{
    position: absolute;
    /* left: 0; */
    right: 0;
    top: 0;
    display: none;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, 0.3);
    font-size: 19px;
    color: #fff;
    border-radius: 3px;
}

.remove-btn:hover{
    font-size: 21px;
}