.pop-up-menu__wrapp{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    transition: all 0.3s;
    z-index: 100;
}

.pop-up-menu {
    position: relative;
    top: 0;
    right: -15px;
    background-color: #fff;
    /* padding: 15px 25px 25px 15px; */
    padding-left: 30px;
    border: 1px solid rgba(0, 0, 0, 0.068);
    box-shadow: -8px 8px 14px 0 rgb(25 42 70 / 11%);
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;
}

.pop-up-menu a {
    text-decoration: none;
    text-align: center;
}

.pop-up-menu__inner{
    padding-right: 23px;

}

.pop-up-menu li {
    position: relative;
    padding: 8px 70px 8px 70px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.068);
    cursor: pointer;
}
.pop-up-menu li:hover:before{
    content: '>';
    position: absolute;
    top: 5px;
    left: 5px;
    color: red;
}

.pop-up-menu li:hover:after{
    content: '<';
    position: absolute;
    top: 5px;
    right: 5px;
    color: red;
}

.pop-up-menu li:hover{
    transform: scale(1.05);
}

.pop-up-menu--hidden{
    right: -300px;
}