.login{
   position: fixed;
   top: 0;
   bottom: 0;
   left: 0;
   right: 0;
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: rgba(0, 0, 0, 0.60);
   z-index: 105; 
}

.login_form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 500px;
    height: 275px;
    /* border: 1px solid white; */
    border-radius: 5px;
    padding: 40px;
    -webkit-box-shadow: 0px 0px 6px 2px rgb(34 60 80 / 22%);
    -moz-box-shadow: 0px 0px 6px 2px rgba(34, 60, 80, 0.22);
    box-shadow: 0px 0px 6px 2px rgb(34 60 80 / 22%);
    background-color: #fff;
}

.login_form input{
    width: 100%;
    /* height: 50px; */
    margin-bottom: 5px;
    outline: none;
    border: 1px solid rgb(214, 214, 214);
    padding: 15px;
    font-size: 25px;
    border-radius: 5px;
    text-align: center;
    color: rgba(105, 105, 105, 0.89);
}

.login_form input::placeholder{
    /* text-align: center; */
    color: rgb(230, 228, 228);
}

.login_btn{
    width: 50%;
    margin-bottom: 5px;
    outline: none;
    border: none;
    padding: 15px;
    font-size: 20px;
    cursor: pointer;
    border-radius: 5px;
    color: white;
    background-color: rgb(77, 192, 66);

}

.login_btn:hover{
    background-color: rgb(104, 214, 94);

}

.login_btn:active{
    transform: scale(0.95);
}