.modal{
    position: fixed;
    top: -100px;
    bottom: 100%;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.329);
    z-index: 40;
    overflow: hidden;
    padding-top: 20px;
}

.modal__content{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    border-radius: 5px;
    padding: 40px;
    -webkit-box-shadow: 0px 0px 6px 2px rgb(34 60 80 / 22%);
    -moz-box-shadow: 0px 0px 6px 2px rgba(34, 60, 80, 0.22);
    box-shadow: 0px 0px 6px 2px rgb(34 60 80 / 22%);
    background-color: #f7f7f8;
    overflow: auto;
}
    

.modal-active{
    bottom: 0;
    top: 0;
}