.card_form{
    width: 100%;
}

.form-input{
    margin-bottom: 20px;
}

.form-select{
    text-align: right;
}

.card_form-row{
    display: flex;
    justify-content: space-between;
}

.form-input label{
    display: block;
    margin-bottom: 5px;
}

.select-category{
    display: flex;
    align-items: center;
    width: 100%;
}


.card_form input, textarea{
    outline: none;
    border: 1px solid rgb(214, 214, 214);
    border-radius: 4px;
    padding: 5px 15px;
    width: 100%;
}

.card_form textarea{
    resize: none;
    width: 100%;
    height: 100px;
}

.new-card-btn{
    cursor: pointer;
}
 